// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import { list } from './menu';
import clipboard from './directives/clipboard';
import DemoBlock from './components/demo-block';
import AsUI from '../package/index';

Vue.directive('copy', clipboard);
Vue.component(DemoBlock.name, DemoBlock);
Vue.use(AsUI);

router.options.routes.forEach(item => {
  if (item.meta && item.meta.group) {
    const hasGroup = list.some(it => it.label === item.meta.group);
    if (!hasGroup) {
      list.push({
        label: item.meta.group,
        children: [
          {
            label: item.meta.title,
            link: item.path
          }
        ]
      });
    } else {
      list.find(it => it.label === item.meta.group).children.push({
        label: item.meta.title,
        link: item.path
      });
    }
  }
});

router.beforeEach((to, from, next) => {
  if (to.meta && to.meta.title) {
    document.title = to.meta.title;
  }
  next();
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
});



